  #main-footer {
    height: auto;
  }
  #main-footer .trampoline,
  #main-footer .issues {
    padding: 5px;
  }
}
@media (max-width: 480px) {
  .grid .tbl,
  .grid .tbl-row,
  .grid .tbl-cell,
  .grid .box {
    display: block;
    width: 100%;
    margin: 0;
  }
  .grid .details .box,
  .grid .charts .box {
    margin: 0 0 10px 0;
  }
}
.clearfix:before,
.clearfix:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}
.clearfix:after {
  clear: both;
}
.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}
.hide {
  display: none !important;
}
.show {
  display: block !important;
}
.invisible {
  visibility: hidden;
}
.text-hide {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}
.hidden {
  display: none !important;
  visibility: hidden !important;
}
.affix {
  position: fixed;
}
